<template>
	<div class="guideMob">
		<guideMobHeader></guideMobHeader>
		<div class="main">
			<h1>
				<span>{{ t(guideItem?.question) }}</span>
			</h1>
			<template v-for="(item, idx) in guideItem?.list" :key="idx">
				<div class="view">
					<p class="text">{{ idx + 1 }} . {{ t(item.txt) }}</p>
					<template v-if="item.img?.length > 0">
						<div class="imgBox" v-for="(i, idx2) in item.img" :key="idx2">
							<el-image :src="i" class="img" :min-scale="0.7" :preview-src-list="item.img" hide-on-click-modal lazy>
								<template #placeholder>
									<div class="image-slot">
										<el-icon>
											<icon-picture/>
										</el-icon>
									</div>
								</template>
							</el-image>
						</div>
					</template>
				</div>
			</template>
		</div>
	</div>
</template>
<script setup>
import {Picture as IconPicture} from "@element-plus/icons-vue";
import GuideMobHeader from "@/pages/guide/components/guideMobHeader.vue";
import {stepList} from "@/data/guide";
import {ElImage} from "element-plus";
import {computed} from "vue";

const guideItem = ref({});
const {t} = useI18n();
definePageMeta({
	layout: "content",
});
const route = useRoute();
const {id} = route.params;
guideItem.value = stepList(id - 1);
const previewList = computed(() => {
	let res = [];
	guideItem.value.list.map((item) => {
		if (item.img) res.push(...item.img);
	});
	return res;
});
</script>
